"use client";

import { useEffect, useState } from "react";
import { usePathname } from "next/navigation";
import Link from "next/link";

const Navbar = () => {
  const [activeItem, setActiveItem] = useState("");
  const pathname = usePathname();

  useEffect(() => {
    switch (pathname) {
      case "/":
        setActiveItem("About");
        break;
      case "/projects":
        setActiveItem("Projects");
        break;
      case "/resume":
        setActiveItem("Resume");
        break;
      case "/contact":
        setActiveItem("Contact");
        break;
      default:
        setActiveItem("");
    }
  }, [pathname]);

  return (
    <div className="p-4 bg-gray-800 text-white">
      <span className="font-bold text-green-400">{activeItem}</span>
      <div className="flex space-x-4 text-red-500 font-bold mt-2">
        {activeItem !== "About" && (
          <Link href="/">
            <span>About</span>
          </Link>
        )}
        {activeItem !== "Projects" && (
          <Link href="/projects">
            <span>Projects</span>
          </Link>
        )}
        {activeItem !== "Resume" && (
          <Link href="/resume">
            <span>Resume</span>
          </Link>
        )}
        {activeItem !== "Contact" && (
          <Link href="/contact">
            <span>Contact</span>
          </Link>
        )}
      </div>
    </div>
  );
};

export default Navbar;
